//using selectors inside the element
// traversing the dom
const btns = document.querySelectorAll(".question-btn");

btns.forEach(function (btn) {
  btn.addEventListener("click", function () {
    const question = btn.parentElement.parentElement; // Get the parent article.question
    const questionText = question.querySelector(".question-text");

    // Close any open question before opening the clicked one
    btns.forEach(function (otherBtn) {
      if (otherBtn !== btn) {
        const otherQuestion = otherBtn.parentElement.parentElement;
        const otherQuestionText = otherQuestion.querySelector(".question-text");
        otherQuestionText.classList.remove("show-text");
      }
    });

    questionText.classList.toggle("show-text");
  });
});
